body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background: url("https://source.unsplash.com/random/1920x1080/?clouds") repeat 0 0;
  -webkit-animation: slide 1000s linear infinite;
}
@-webkit-keyframes slide {
  from { background-position: 0, 0;} 
  to {background-position: -1920px, 0;}
}
.card {
  background: #000000a0;
  color: whitesmoke;
  padding: 1em;
  border: whitesmoke solid 1vh;
  border-radius: 30px;
  width: 100%;
  height: 50px;
  overflow: hidden;
  max-width: 600px;
  margin: 10px;
  align-items: stretch;
}
.card.visible{
  height: 470px;
  transition: height ease 1.5s;
}

