.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
li{ 
  display: flex;
  justify-content:start;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(30, 29, 29, 0.726);
  -webkit-transition: .4s;
  transition: .4s;
  cursor: default;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: whitesmoke;
  -webkit-transition: .4s;
  transition: .4s;
}
.slider:hover:before {
  background-color: greenyellow;
}
.slider:after{
  justify-content: center;
  overflow: hidden;
  content:" f °  c °";
  color: white;
}
input:checked + .slider {
  background-color: rgba(30, 29, 29, 0.726);
}
input:focus + .slider {
  box-shadow: 0 0 1px rgba(30, 29, 29, 0.726);
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.slider.round {
  border-radius: 34px;
  padding-top: 5px;
  padding-left: 11px;
}
.slider.round:before {
  border-radius: 50%;
}

@media only screen and (max-width:500px){
  .futureWeather{
      justify-content: start;
      align-items:none;
      overflow-y: scroll;
      -ms-overflow-style: none;  /* IE and Edge */
      scrollbar-width: none;  /* Firefox */
  }
  .futureWeather::-webkit-scrollbar { /* Chrome, Safari */
      display: none;
  }
}